import { useState } from 'react';
import { __ } from '@wordpress/i18n';
import { InspectorControls, BlockControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, PanelRow, TextControl, RangeControl, ToolbarGroup, ToolbarButton, __experimentalUnitControl as UnitControl, Tooltip, Button, Dashicon } from '@wordpress/components';
import { produce } from 'immer';

// Settings Components
import { Label, Background, BDevice, ColorsControl, HelpPanel, InlineMediaUpload, ShadowControl, Typography } from '../../../../../Components';
import { gearIcon } from '../../../../../Components/utils/icons';
import { pxUnit, perUnit, emUnit } from '../../../../../Components/utils/options';

import { generalStyleTabs } from '../../../utils/options';

const Settings = ({ attributes, setAttributes, activeIndex, setActiveIndex }) => {
	const { gallery, slideSpeed, itemHeight, columns, columnGap, rowGap, background, shadow, hoverShadow, titleTypo, subtitleTypo } = attributes;

	const [device, setDevice] = useState('desktop');

	const addGalleryItem = () => {
		setAttributes({
			gallery: [...gallery, {
				title: `Title ${gallery.length + 1}`,
				subtitle: `Subtitle ${gallery.length + 1}`,
				images: [],
				colors: {
					color: '#fff',
					bgType: 'solid',
					bg: '#505a64d9'
				},
				btnColors: {
					color: '#fff',
					bgType: 'solid',
					bg: '#000a1480'
				}
			}]
		});
		setActiveIndex(gallery.length);
	}

	const updateGallery = (type, val, otherIndex = false) => {
		const newGallery = produce(gallery, draft => {
			if (false !== otherIndex) {
				draft[activeIndex][type][otherIndex] = val;
			} else {
				draft[activeIndex][type] = val;
			}
		});
		setAttributes({ gallery: newGallery });
	}

	const duplicateGallery = e => {
		e.preventDefault();

		setAttributes({ gallery: [...gallery.slice(0, activeIndex), { ...gallery[activeIndex] }, ...gallery.slice(activeIndex)] });

		setActiveIndex(activeIndex + 1);
	}

	const removeGallery = e => {
		e.preventDefault();

		setAttributes({ gallery: [...gallery.slice(0, activeIndex), ...gallery.slice(activeIndex + 1)] });

		setActiveIndex(0 === activeIndex ? 0 : activeIndex - 1);
	}

	const { title = '', subtitle = '', images = [], colors = {}, btnColors = {} } = gallery[activeIndex] || {};

	return <>
		<InspectorControls>
			<div className='bigbInspectorInfo'>
				Need more block like this? Checkout the bundle  <a href='https://wordpress.org/plugins/b-blocks' target='_blank' rel='noopener noreferrer'>B Blocks</a>
			</div>

			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs}>{tab => <>
				{'general' === tab.name && <>
					<HelpPanel slug='3d-image-gallery' docsLink='https://bblockswp.com/docs/3d-image-gallery-block' />


					<PanelBody className='bPlPanelBody addRemoveItems' title={__('Add or Remove Items', 'image-gallery')}>
						{null !== activeIndex && <PanelBody className='bPlPanelBody editItem' title={__(`Item ${activeIndex + 1}:`, 'image-gallery')} initialOpen={true}>
							<Label className='mb5'>{__('Title:', 'image-gallery')}</Label>
							<TextControl value={title} onChange={val => updateGallery('title', val)} />

							<Label>{__('Subtitle:', 'image-gallery')}</Label>
							<TextControl value={subtitle} onChange={val => updateGallery('subtitle', val)} />

							<Label>{__('Images:', 'image-gallery')}</Label>
							{images.map((img, i) => <PanelRow key={i}>
								<InlineMediaUpload value={img} types={['image']} onChange={val => updateGallery('images', val, i)} placeholder={__('Enter gallery image url', 'image-gallery')} />

								<Tooltip text={__('Remove this Image', 'image-gallery')} placement='top' position='top'>
									<Button onClick={e => {
										e.preventDefault();
										updateGallery('images', [...images.slice(0, i), ...images.slice(i + 1)]);
									}} ><Dashicon icon='no' /></Button>
								</Tooltip>
							</PanelRow>)}

							<div className='addItem'>
								<Button label={__('Add Image', 'image-gallery')} onClick={() => updateGallery('images', [...images, ''])}><Dashicon icon='plus' />{__('Add Image', 'image-gallery')}</Button>
							</div>

							<ColorsControl className='mt20' value={colors} onChange={val => updateGallery('colors', val)} defaults={{ color: '#fff', bgType: 'solid', bg: '#505a64d9' }} />

							<ColorsControl label={__('Button Colors:', 'image-gallery')} value={btnColors} onChange={val => updateGallery('btnColors', val)} defaults={{ color: '#fff', bgType: 'solid', bg: '#000a1480' }} />

							<PanelRow className='itemAction mt20'>
								<Button className='removeItem' label={__('Remove', 'image-gallery')} onClick={removeGallery} ><Dashicon icon='no' />{__('Remove', 'image-gallery')}</Button>

								<Button className='duplicateItem' label={__('Duplicate', 'image-gallery')} onClick={duplicateGallery} >{gearIcon}{__('Duplicate', 'image-gallery')}</Button>
							</PanelRow>
						</PanelBody>}

						<div className='addItem'>
							<Button label={__('Add New Gallery Item', 'image-gallery')} onClick={() => addGalleryItem()}><Dashicon icon='plus' />{__('Add New Gallery Item', 'image-gallery')}</Button>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Gallery Settings', 'image-gallery')} initialOpen={false}>
						<Label className='mb5'>{__('Slide Speed (in seconds):', 'image-gallery')}</Label>
						<RangeControl value={slideSpeed} onChange={val => setAttributes({ slideSpeed: val })} min={0} max={10} step={0.01} beforeIcon='slides' />

						<UnitControl className='mt20' label={__('Item Height:', 'image-gallery')} labelPosition='left' value={itemHeight} onChange={val => setAttributes({ itemHeight: val })} units={[pxUnit(), perUnit(), emUnit()]} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Layout Settings', 'image-gallery')} initialOpen={false}>
						<PanelRow>
							<Label className='mb5'>{__('Columns:', 'image-gallery')}</Label>
							<BDevice device={device} onChange={val => setDevice(val)} />
						</PanelRow>
						<RangeControl value={columns[device]} onChange={val => { setAttributes({ columns: { ...columns, [device]: val } }) }} min={1} max={6} step={1} beforeIcon='grid-view' />

						<UnitControl className='mt20' label={__('Column Gap:', 'image-gallery')} labelPosition='left' value={columnGap} onChange={val => setAttributes({ columnGap: val })} units={[pxUnit(), perUnit(), emUnit()]} />

						<UnitControl className='mt20' label={__('Row Gap:', 'image-gallery')} labelPosition='left' value={rowGap} onChange={val => setAttributes({ rowGap: val })} units={[pxUnit(), perUnit(), emUnit()]} />
					</PanelBody>
				</>}


				{'style' === tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Gallery', 'image-gallery')}>
						<Background value={background} onChange={val => setAttributes({ background: val })} defaults={{ type: 'gradient', gradient: 'linear-gradient(#ff5f6d, #ffc371)' }} isImage={false} />

						<ShadowControl label={__('Shadow:', 'image-gallery')} value={shadow} onChange={val => setAttributes({ shadow: val })} defaults={{ vOffset: '4px', blur: '4px', spreed: '0px', color: '#000a1459' }} />

						<ShadowControl label={__('Hover Shadow:', 'image-gallery')} value={hoverShadow} onChange={val => setAttributes({ hoverShadow: val })} defaults={{ vOffset: '12px', blur: '20px', spreed: '-12px', color: '#000a14cc' }} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Title & Subtitle', 'image-gallery')} initialOpen={false}>
						<Typography label={__('Title Typography', 'image-gallery')} value={titleTypo} onChange={val => setAttributes({ titleTypo: val })} defaults={{ fontSize: { desktop: 48, tablet: 40, mobile: 32 } }} />

						<Typography label={__('Subtitle Typography', 'image-gallery')} value={subtitleTypo} onChange={val => setAttributes({ subtitleTypo: val })} defaults={{ fontSize: { desktop: 20, tablet: 18, mobile: 16 } }} />
					</PanelBody>
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<ToolbarGroup className='bPlToolbar'>
				<ToolbarButton label={__('Add New Gallery Item', 'image-gallery')} onClick={() => addGalleryItem()}><Dashicon icon='plus' /></ToolbarButton>
			</ToolbarGroup>
		</BlockControls>
	</>;
};
export default Settings;